@charset "utf-8";

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');

*{margin:0px;padding:0px;}
body{
  font-family: "Noto Sans KR", sans-serif;
  font-size: 13px;
  color:#333;
  background-color: #eff3f7;
}
a{
  color:#333;text-decoration: none;
  font-size: 16px;
}
ul,li{
  list-style: none;
}

/* 공통서식 */
/* 상단헤더서식 */
.header{
  width:200px;height:100%;
  text-align: center;
  position:fixed;
  top:0px;left:0px;
  z-index:100;
}
h1{
  padding:70px 0px 50px 0px;
}
/* nav서식 */
nav{
  padding-top:70px;
  text-align: center;
}
nav li{line-height: 40px;}
nav a{
  text-transform: uppercase;font-weight: bold;
}
nav a:hover{
  text-decoration: underline;
}
/* 메인서식 */
main{
  width:calc(100% - 200px);
  height:100%;
  position: fixed;
  left:200px;
  top:0px;
  padding-top :50px;
  overflow: scroll;
  margin-bottom: 50px;


}
.loading_bar{
  width:100%;
  height:100%;
  text-align: center;
}
.loading_bar img{
  position: relative;
  top:300px;
}
.footer{
  position: fixed;
  bottom:0px;
  width:100%;line-height: 40px;
  background: #333;
}
.footer address{
  color:#fff;text-align: center;
  font-style: normal;
}

