@charset "utf-8";

.movie{

}

.movie ul{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding:5%;
  gap: 3%;
}
.movie li{
  
  background: #fff;
  width:650px;height:300px;
  /* border:1px solid #ddd; */
  box-shadow: 2px 2px 5px #ccc;
}
.movie li > div{
  padding:30px;
  display: flex;
  gap:30px;
  position: relative;
}

.info_box{}
.poster{
  perspective: 1000px;
  transform: translateY(-45px);
}
.movie li > div img{
  display: block;
  width: 150px;
  height: 250px;
  object-fit: cover;
  /* transform-origin: top center; 위쪽 고정 */
  transition: transform 0.7s ease;
  box-shadow: 3px 15px 15px #ccc;
}
.movie li > div img:hover{
    transform: rotateX(20deg) ;
  
}
.title01{
  font-size: 1.2rem;
  font-weight: bold;
}
.year{
  color:rgb(20, 143, 143);
  font-weight: 500;
}
.rating{
  position:absolute;
  bottom:50px;right:50px;
  font-weight: bold;
  color:#f00;
}

